.headerWrapper {
    background-color: #dbb963;
    width: 80%;
    margin: 0 auto;
    padding: 0 17px 15px 17px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    .headerContent {
        z-index: 1000;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #efd591;
        a {
            display: block;
            color: black;
            text-decoration: none;
            div {
                margin: 0 10px;
                font-weight: 800;
                font-size: 24px; }
            .main {
                font-size: 32px; } } } }
